export default (editor, opts = {}) => {
    const {
        id, label, classPrefix, linkType,
        idList, labelList,
        idItemLink, labelItemLink,
        idItemText, labelItemText,
    } = opts;

    // item text
    editor.DomComponents.addType(idItemText, {
        model: {
            defaults: {
                name: labelItemText,
                tagName: 'li',
                attributes: {
                    "class": `${classPrefix}-item`,
                },
                components: {
                    type: 'text',
                    tagName: 'span',
                    components: editor.I18n.t("grapesjs-plugin-bootstrap-breadcrumb.homeTxt"),
                },
                droppable: false,
                draggable: (target, destination) => {
                    return destination.get('type') === idList;
                },
            }
        }
    })

    // item link
    editor.DomComponents.addType(idItemLink, {
        model: {
            defaults: {
                name: labelItemLink,
                tagName: 'li',
                attributes: {
                    "class": `${classPrefix}-item`,
                },
                components: {
                    type: linkType,
                    tagName: 'a',
                    attributes: {
                        href: '#',
                    },
                    components: editor.I18n.t("grapesjs-plugin-bootstrap-breadcrumb.homeTxt"),
                },
                droppable: false,
                draggable: (target, destination) => {
                    return destination.get('type') === idList;
                },
            }
        }
    })


    // list
    editor.DomComponents.addType(idList, {
        model: {
            defaults: {
                name: labelList,
                tagName: 'ol',
                attributes: {
                    class: `${classPrefix} mb-0`,
                    "active-last": "1",
                },
                components: [],
                droppable: (target, destination) => {
                    return target.get('type') === idItemLink || target.get('type') === idItemText;
                },
                draggable: (target, destination) => {
                    return destination.get('type') === id;
                },
                "active-last": "1",
                traits: [
                    {
                        name: 'active-last',
                        label: 'Active last item',
                        type: 'checkbox',
                        valueTrue: '1',
                        valueFalse: '0',
                        changeProp: 1,
                    }
                ],
                'script-props': ['active-last'],
                script: function () {
                    $(document).ready(function () {
                        if ($(this).attr('active-last') === '1') {
                            $(this).find('li').last().addClass('active').attr("aria-current", "page");
                        }
                    });
                },
            },
            init() {
                this.on('change:active-last', this.handleChangeActiveLast);
                this.on('component:update', this.handleComponentChange);
                
                if (this.components().length === 0){
                    this.components().add({
                        type: idItemLink,
                        components: {
                            type: linkType,
                            tagName: 'a',
                            attributes: {
                                href: '#',
                            },
                            components: `Home`
                        }
                    });
    
                    this.components().add({
                        type: idItemText,
                        components: {
                            type: 'text',
                            tagName: 'span',
                            components: `Page`,
                        }
                    });
                }
            },
            handleChangeActiveLast() {
                this.setAttributes({ 'active-last': this.get('active-last') });
                this.handleComponentChange();
            },
            handleComponentChange() {
                let modelDataList = this.components().models;
                modelDataList.map(
                    (modelData, idx) => {
                        let clsOfModel = modelData.getAttributes().class;
                        if (this.get('active-last') === '1'){
                            if (idx === modelDataList.length - 1 && clsOfModel.indexOf('active') === -1)  clsOfModel += ' active';
                            else if (idx !== modelDataList.length - 1 && clsOfModel.indexOf('active') !== -1) clsOfModel = clsOfModel.replaceAll('active', '');
                        } else if (clsOfModel.indexOf('active') !== -1) clsOfModel = clsOfModel.replaceAll('active', '');
                        modelData.setAttributes({ class: clsOfModel });
                    }
                )
            }
        }
    })

    // main
    editor.DomComponents.addType(id, {
        model: {
            defaults: {
                name: label,
                tagName: 'nav',
                attributes: {
                    "aria-label": "breadcrumb",
                },
                components: { type: idList },
                droppable: (target, destination) => {
                    return target.get('type') === idList;
                },
                draggable: true,
            },

        },
    });
};
