import { dataConst } from "./const";

export default (editor, opts = {}) => {
    let { 
        id, label, catagoryLabel,
        idItemLink, labelItemLink,
        idItemText, labelItemText,
     } = opts;

    editor.BlockManager.add(id, {
        category: catagoryLabel,
        label: label,
        content: { type: id },
        media: dataConst.iconBlock,
    });

    editor.BlockManager.add(idItemLink, {
        category: catagoryLabel,
        label: labelItemLink,
        content: { type: idItemLink },
        media: dataConst.iconItemLink,
    });

    editor.BlockManager.add(idItemText, {
        category: catagoryLabel,
        label: labelItemText,
        content: { type: idItemText },
        media: dataConst.iconItemText,
    });
}
