import loadComponents from './components';
import loadBlocks from './blocks';
import en from './locale/en';

export default (editor, opts = {}) => {
    const options = {
        ...{
            i18n: {},
            id: 'bootstrap-breadcumb',
            label: 'Breadcumb',
            classPrefix: 'breadcrumb',
            linkType: 'link',
            idList: 'bootstrap-breadcumb-list', 
            labelList: 'Breadcumb List',
            idItemLink: `bootstrap-breadcumb-item-link`, 
            labelItemLink: `Breadcumb Item Link`,
            idItemText: `bootstrap-breadcumb-item-text`, 
            labelItemText: `Breadcumb Item Text`,
            catagoryLabel: 'Navigations',
        }, ...opts
    };

    editor.I18n && editor.I18n.addMessages({en, ...options.i18n});

    loadComponents(editor, options);
    loadBlocks(editor, options);
};